.sideBarRow {
  display: flex;
  align-items: center;
  cursor: pointer;
  /* border: 0.1px solid lightgray; */
}
/* .sideBarRow:hover {
  background-color: rgba(211, 211, 211, 0.176);
} */
.sideBarRow__icon {
  margin: 1rem 1rem;
  color: gray;
  font-size: 20px !important;
}
.sideBarRow:hover > .sideBarRow__icon {
  color: #ff0004a1;
}
.sideBarRow:hover > .sideBarRow__title {
  color: black;
}
.sideBarRow__title {
  color: gray;
  margin-right: 1rem;
  font-size: small;
  margin-top: 10px;
}

/* .sideBarRow.selected {
  background-color: lightgray;
} */
.sideBarRow.selected > .sideBarRow__title {
  font-weight: 600;
  color: black;
}
.sideBarRow.selected > .sideBarRow__icon {
  color: #ff0004a7;
}
