.teacherDashSideOpt {
  display: flex;
  align-items: center;
  cursor: pointer;

  /* border: 0.1px solid lightgray; */
}
/* .teacherDashSideOpt:hover {
   background-color: rgba(211, 211, 211, 0.176);
 } */
.teacherDashSideOpt__icon {
  margin: 1rem 1rem;
  color: gray;
  font-size: 20px !important;
}
.teacherDashSideOpt:hover > .teacherDashSideOpt__icon {
  color: #ff0004a1;
}
.teacherDashSideOpt:hover > .teacherDashSideOpt__title {
  color: black;
}
.teacherDashSideOpt__title {
  color: gray;
  margin-right: 1rem;
  font-size: small;
  margin-top: 10px;
}

/* .teacherDashSideOpt.selected {
   background-color: lightgray;
 } */
.teacherDashSideOpt.selected > .teacherDashSideOpt__title {
  font-weight: 600;
  color: black;
}
.teacherDashSideOpt.selected > .teacherDashSideOpt__icon {
  color: #ff0004a7;
}
