.subtotal {
     display: flex;
     flex-direction: column;
     justify-content: space-between;
     width: 300px;
     height: 150px;
     padding: 20px;
     background-color: #f3f3f3;
     border-radius: 3px;
     border: 1px solid #dddddd;
}
.subtotal__gift {
     display: flex;
     align-items: center;
     /* justify-content: space-around; */
}
.subtotal__gift > input {
     margin-right: 5px;
}
.subtotal > button {
     background-color: rgb(255, 108, 108);
     border-radius: 3px;
     width: 100%;
     height: 30px;
     border: 1px solid;
     margin-top: 10px;
     border-color: #a88734 #927e31 #7e6527;
     color: #111;
}
