.footerMain-container {
  height: 60%;
  width: 90vw;
  margin: 0 auto;
  /* border: 2px solid black; */
}
.footerMain-upper {
  display: flex;
  /* border: 2px solid black; */
  justify-content: space-between;
  margin: 2rem;
  flex-wrap: wrap;
}
.footerMain-left {
  display: flex;
  /* border: 2px solid black; */
  flex: 0.8;
  flex-wrap: wrap;
}
.footerMain-column {
  margin: 0rem 4rem;
}
.footerMain-right {
  display: flex;
  flex-direction: column;
  /* border: 2px solid black; */
}
.footerMain-img {
  max-height: 50px;
  /* width: 50px; */
  flex: 0.2;
}
.footerMain-lower {
  border-top: 1px solid lightgray;
  padding-top: 3rem;
  margin: 2rem;
}
