/* TEST */

.card {
  display: flex;
  /* flex-direction: row; */
  flex-wrap: wrap;
  margin: 1rem;
  margin-right: 1rem;
  width: 100%;
  transition: 0.3s;
  padding: 1rem;
}

.img {
  margin: 1rem;
  max-height: 200px;
  object-fit: contain;
  max-width: 250px;
}
.test_info {
  margin: 1rem;
}
.test__button {
  margin-bottom: 1rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  background-color: white;
  border: 2px solid #ffe799;
  transition: 0.5s;
}
.test__button:hover {
  /* transform: translateX(200px); */
  background-color: #ffe799;
}
.test__button_wish {
  margin-bottom: 1rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: 0.5s;
  background-color: white;
  border: 2px solid lightgray;
}
.test__button_wish:hover {
  /* transform: translate3d(100px); */
  background-color: lightgray;
}
.teat__star {
  display: flex;
  flex-wrap: wrap;
}
.test_info > h4 {
  display: flex;
  flex-wrap: wrap;
}
.test__container {
  display: flex;
  align-items: center;
  margin: 0.7rem;
}

