.teacher__dashboard__container {
  display: flex;
  max-width: 60vw;
  margin: 3rem auto;
  flex-direction: column;
}
.teacher__dashboard__header {
  margin: auto;
}
.teacher__dashboard__img {
  border-radius: 100%;
  max-height: 200px;
}

.teacher__dashboard__form {
  width: 30vw;
  padding: 0rem 0rem 2rem 0rem;

  margin-top: 3rem;
}
.teacher__dashboard__inputContainer {
  border: 1px solid lightgray;
  border-radius: 100px;
  width: 20vw;
  padding: 0.3rem 0.8rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.teacher__dashboard__input {
  width: 100%;
  border: none;
  outline: none;
}
.teacher__dashboard__form__buttons {
  width: 20vw !important;
  border-radius: 100px !important;
  outline: none !important;
  color: red !important;
  margin-top: 1rem !important;
  /* border: 2px solid black; */
}
