.profile {
  display: flex;
  margin: 3rem;
  box-shadow: 4px 7px 10px -12px black;
  display: flex;
  flex-wrap: wrap;
  /* border: 1px solid black; */
  background-color: #ecf2f8;
  border-radius: 10px;
  /* flex-direction: column; */
}
.right {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px auto;
}
.profile__left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* border-right: 1px solid gray; */
  margin: 0.6rem;
  /* justify-content: space-between; */
}
.profile__leftInfo {
  /* border: 2px solid black; */
  margin-top: 2rem;
}
.profile__leftInfo > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
}

.profile__leftInfo p {
  margin-top: 1rem;
  /* flex-wrap: wrap; */
}
.profile__rightButton {
  border-radius: 2px;
  /* width: 100%; */
  /* width: 100px; */
  height: 30px;
  border-color: darkgray;
  margin-top: 6px;
  margin-right: 3px;
  border-radius: 5px;
  border: none;
}
