.teacherCourse {
  margin: 2rem;
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  height: 100vh;
}
.teacherCourse__header {
  /* border: 1px solid black; */
  margin: 0rem auto;
}
.teacherCourse__container {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin-top: 2rem;
}
.teacherCourse__card {
  border: 1px solid lightgray;
  min-height: 300px;
  max-height: 350px;
  margin: 1rem 1rem;
}
.teacherCourse__img {
  max-height: 400px;
  width: 300px;
}
.teacherCourse__textContainer {
  margin-left: 10px;
}

.teacherCourse__title {
  width: 200px;
}
.teacherCourse__bottom {
  display: flex;
  align-items: center !important;
  justify-content: space-between;
  /* border: 2px solid black; */
}
.teacherCourse__button {
  margin-right: 2rem !important;
}
.teacherCourse__price {
  /* border: 2px solid green; */
  margin-top: 10px;
}
