.login {
     display: flex;
     flex-direction: column;
     align-items: center;
     height: 100vh;
     background-color: white;
     /* width: 30%; */
     /* justify-content: center; */
}
.login__logo {
     max-width: 150px;
     min-width: 100px;
     width: 150px;
     object-fit: contain;
     margin: 0 20px;
     margin-top: 18px;
     margin-left: auto;
     margin-right: auto;
     margin-bottom: 3rem;
     height: 80%;
     color: lightgray;
}
.login__container {
     margin-top: 3rem;
     width: 400px;
     height: fit-content;
     display: flex;
     flex-direction: column;
     border-radius: 5px;
     border: 1px solid lightgray;
     padding: 20px;
}

.login__container > h1 {
     font-weight: 500;

     margin-bottom: 20px;
}

.login__container > form > h5 {
     margin-bottom: 5px;
}

.login__container > form > input {
     height: 30px;
     margin-bottom: 10px;
     background-color: white;
     width: 98%;
}
.login__container > p {
     margin: 15px 15px 15px 0px;
     font-size: 12px;
}

.login__signInButton {
     background-color: #f0c14b;
     border: 1px solid;
     margin-top: 6px;
     width: 100%;
     height: 30px;
     border-color: #a88734 #927e31 #7e6527;
     color: #111;
}
.login__registerButton {
     border-radius: 2px;
     width: 100%;
     border-color: darkgray;
     margin-top: 6px;
}
