.home {
  display: flex;
  flex-direction: column;
}
.home__image {
  width: 100%;
  /* height: 500px; */
  /* min-width: 10px; */
  z-index: -1;
  margin-bottom: -100px;
  /* mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)); */
}
.home__test {
  margin: 100px 3rem;
  /* border: 2px solid violet; */

  /* flex-grow: 1;
  flex-shrink: 1; */
}
.home__test h4 {
  display: block;
  margin-top: 2rem;
}
.home__test__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0px auto;
}
.home__right__container {
  display: flex;
  flex: 0.8;
  justify-content: center;
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
  min-width: 400px;
  background-color: whitesmoke;
}

@media (min-width: 1000px) {
  .home__test__inner {
    transition: 3s;
  }
}

