.header__personIcon {
  align-items: center;
  margin-top: 4px;
}
.header {
  display: flex;
  min-height: 50px;
  width: 100%;
  align-items: center;
  background-color: #131921;
  position: sticky;
  top: 0;
  z-index: 100;
  min-width: 400px;
  max-width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  box-shadow: 0 0 10px -6px black;
  height: auto;
}
.header__logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  /* margin-top: 18px; */
  height: 80%;
  color: lightgray;
}
.header__corona__container{
  display: flex;
  /* text-align: center; */
  justify-content: center;
  /* align-items: center; */
  text-decoration: none;

}
.header__corona__logo{
  width: 30px;
  object-fit: contain;
  margin: 0 20px;
  /* margin-top: 18px; */
  height: 20%;
  color: lightgray;
  margin-top: .5rem;
  /* text-align: center; */
}
.header__corona__text{
  /* text-align: center; */
  text-decoration: none;
  color: aliceblue;
  margin-top: .7rem;
  text-decoration-line: none;

}
.header__searchIcon {
  padding: 5px;
  height: 22px;
  font-size: 30px !important;
  color: gray;
  cursor: pointer;
  /* background-color: #ea5252; */
}
.header__searchIcon :hover {
  color: black;
}
.header_search {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  border-radius: 100px;
  padding: 1px 10px;
  margin: 0 2rem;
  background-color: white;
  /* height: ; */
}
.header__searchInput {
  border: none;
  outline: none;
  padding: 0px 10px;
  width: 40vw;
  font-size: 15px;
}
.header__link {
  color: black !important;
  text-decoration: none !important;
}
.header__nav {
  width: 20vw;
  display: flex;
  justify-content: space-evenly;
}

.header__option {
  display: flex;
  color: white;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
}

.header__optionLineOne {
  font-size: 10px;
}
.header__optionLineTwo {
  font-size: 15px;
  font-weight: 800;
}
.header__optionBasket {
  display: flex;
  align-items: center;
  color: white;
}
.header__basketCount {
  margin-right: 10px;
  margin-left: 10px;
}
